import { BaseValueObject } from '../Base';

export const PROJECT_STATUS_CODE_LIST = [
  'NOT_STARTED',
  'DESIGN_IN_PROGRESS',
  'START_AT_NEXT_MONTH',
  'IN_PROGRESS_AT_SITE',
  'CONSTRUCTION_COMPLETE',
  'MONTHLY_REPORT_COMPLETE',
  'PAYMENT_RECEIVED',
  'ABORTED',
  'DELETED',
] as const;

export type PROJECT_STATUS_CODE_TYPE =
  (typeof PROJECT_STATUS_CODE_LIST)[number];

export class ProjectStatusCode extends BaseValueObject<
  'ProjectStatusCode',
  string
> {
  static from(value: string): ProjectStatusCode {
    return new ProjectStatusCode(value);
  }

  protected validate(value: string): string {
    if (!PROJECT_STATUS_CODE_LIST.includes(value as PROJECT_STATUS_CODE_TYPE)) {
      throw new Error('Invalid project status code');
    }

    return value;
  }
}
